import React from 'react';
import Media from 'react-media';
import style from './portfolio.module.css';
import Slider from './portfolioSlider/portfolioSlider';
import SliderDesk from './portfolioSlider/portfolioSliderDesk';

const Portfolio = ({ onOpenModal }) => (
  <section className={style.section} id="Portfolio" name="Portfolio">
    <h2 className={style.header}>ПОРТФОЛИО</h2>
    <Media query={{ maxWidth: 1199 }}>
      {matches =>
        matches ? (
          <Slider onOpenModal={onOpenModal} />
        ) : (
          <SliderDesk onOpenModal={onOpenModal} />
        )
      }
    </Media>
  </section>
);

export default Portfolio;
