import React from 'react';
import style from './carouselChild.module.css';

const Child = ({ city, onOpenModal }) => {
  const {
    logo,
    logoW,
    name,
    text = [],
    background,
    backgroundW,
    button,
  } = city;
  return (
    <div className={style.slide}>
      <div className={style.wrapperLogo}>
        <picture>
          <source srcSet={logoW} type="image/webp" />
          <source srcSet={logo} type="image/png" />
          <img className={style.logo} src={logo} alt="City Logo" />
        </picture>
      </div>
      <div className={style.textContainer}>
        <div className={style.imgWrapper}>
          <picture>
            <source srcSet={backgroundW} type="image/webp" />
            <source srcSet={background} type="image/jpeg" />
            <img
              className={style.backgroundImg}
              src={background}
              alt="CityBackground"
            />
          </picture>
          <h3 className={style.city}>{name}</h3>
          {text.map(enter => (
            <div className={style.wrapperList} key={enter.title}>
              <h4 className={style.direction}>{enter.title}</h4>
              <ul className={style.directionList}>
                {enter.list.map(listItem => (
                  <li className={style.directionListItem} key={listItem}>
                    <span className={style.directionListItemText}>
                      {listItem}
                    </span>
                  </li>
                ))}
              </ul>
              {button && (
                <button
                  onClick={onOpenModal}
                  type="button"
                  className={style.buttonItem}
                >
                  подключить
                </button>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Child;
