import React from 'react';
import Carousel from 'nuka-carousel';
import Child from './carouselChildren/carouselChild';
import CarouselBottomControls from './carouselBottomControls/carouselBottomControls';
import style from './portfolioSliderDesk.module.css';
import cities from './cities';

const SliderDesk = ({ onOpenModal }) => (
  <Carousel
    renderTopCenterControls={null}
    renderCenterLeftControls={({ previousSlide }) => (
      <button className={style.button} type="button" onClick={previousSlide}>
        <i className="material-icons">arrow_back_ios</i>
      </button>
    )}
    renderCenterRightControls={({ nextSlide }) => (
      <button className={style.button} type="button" onClick={nextSlide}>
        <i className="material-icons">arrow_forward_ios</i>
      </button>
    )}
    renderBottomCenterControls={obj => <CarouselBottomControls obj={obj} />}
    slidesToShow={1}
    slideWidth="400px"
    transitionMode="scroll3d"
    opacityScale={0.2}
  >
    {cities.map(city => (
      <Child onOpenModal={onOpenModal} key={city.id} city={city} />
    ))}
  </Carousel>
);

export default SliderDesk;
